@charset "utf-8";

.blind {
  display: none;
}
html {
  height: 100%;
  overflow-y: auto;
}

body {
  overflow: hidden;
  margin-top: 0 !important;
  min-width: 320px;
  color: #333;
  line-height: 1.6;
  font-size: 16px;
  font-family: "El Messiri", sans-serif;
  word-break: break-all;
  word-wrap: break-word;
  word-break: keep-all;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "El Messiri", sans-serif;
}

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
input,
textarea,
blockquote,
th,
td,
p,
button {
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
}

fieldset,
img,
abbr,
acronym {
  border: 0 none;
}

legend {
  position: absolute;
  height: 0;
  line-height: 0;
  font-size: 0;
  overflow: hidden;
}

legend.showlegend {
  position: static;
  line-height: 14px;
  font-size: 12px;
  overflow: visible;
}

address,
caption,
cite,
code,
dfn,
em,
var {
  font-style: normal;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

q:before,
q:after {
  content: "";
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

caption {
  height: 0;
  line-height: 0;
  font-size: 0;
  overflow: hidden;
  text-indent: -10000px;
}

hr {
  display: none;
}

a {
  color: #333;
  text-decoration: none;
  cursor: pointer;
}

button {
  border: 0 none;
  background: none;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

button,
input,
select,
textarea {
  color: #555;
  line-height: 19px;
  font-size: 13px;
  font-family: "Roboto", sans-serif;
  vertical-align: middle;
}

select {
  -webkit-border-radius: 0;
}

select {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;

  background-repeat: no-repeat;
  background-position: calc(100% - 15px) center;
  background-position: -webkit-calc(100% - 15px) center;
  background-position: -moz-calc(100% - 15px) center;
}

select::-ms-expand {
  display: none;
}

input[type="text"],
input[type="password"],
input[type="number"],
input[type="tel"],
input[type="email"],
input[type="button"],
input[type="submit"],
textarea {
  border-radius: 0 !important;
  -webkit-appearance: none;
  -moz-appearance: none;
}

a[href^="tel"] {
  color: inherit;
  /* Inherit text color of parent element. */
  text-decoration: none;
  /* Remove underline. */
  /* Additional css `propery: value;` pairs here */
}

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  cursor: pointer;
  overflow: visible;
  -webkit-appearance: button;
}

input[type="checkbox"],
input[type="radio"] {
  width: 14px;
  height: 14px;
  line-height: 14px;
  box-sizing: border-box;
  -webkit-border-radius: 0;
}

input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}

input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

input::-webkit-input-placeholder {
  color: #a0a0a0;
}

input:-moz-input-placeholder {
  color: #a0a0a0;
}

input::-moz-input-placeholder {
  color: #a0a0a0;
}

input:-ms-input-placeholder {
  color: #a0a0a0;
}

img {
  vertical-align: top;
}

textarea {
  overflow: auto;
  resize: none;
}

textarea::-webkit-input-placeholder {
  color: #a0a0a0;
}

textarea:-moz-placeholder {
  /* Firefox 18- */
  color: #a0a0a0;
}

textarea::-moz-placeholder {
  /* Firefox 19+ */
  color: #a0a0a0;
}

textarea:-ms-input-placeholder {
  color: #a0a0a0;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
object {
  display: block;
  margin: 0;
  padding: 0;
}

audio[controls],
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}

/* Disable orange highlight */
label,
a,
button {
  -o-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-tap-highlight-color: rgba(0, 0, 0, 0);
  -moz-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
